//
// Type
// --------------------------------------------------

// Paragraphs
p {
	-webkit-margin-before:	0;
	-webkit-margin-after:	0;
	margin:					0;
	letter-spacing:			1px; }


// Input Text
.input {
	color:				$calc-input-color;
	text-align:			right;
	font-size:			14px;
	height:				16px;
	margin-bottom:		12px; }


// Display Text
.answer {
	font-size:		20px;
	height:			23px; }


// Warning Text
.answer.warning {
	font-family:	'Coverface SE Bold', sans-serif;
	font-size:		14px;
	padding-top:	4px; }


// Anchors
a {
	color:			#00695c; }

// Credits
.credits {
	p {
		font-family:	Arial, Helvetica, sans-serif;
		font-size:		13px;
		margin-top:		10px;
		text-align:		center; } }
